// Define el reducer
const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case CAMBIAR_IDIOMA:
      let idiomaActual = state.idioma === "es" ? "en" : "es";
      return {
        ...state,
        idioma: idiomaActual,
      };
    case CAMBIAR_MODO_COLOR:
      let colorActual = state.modoColor === "claro" ? "oscuro" : "claro";
      return {
        ...state,
        modoColor: colorActual,
      };
    case PAUSAR_ANIMACION:
      return {
        ...state,
        pauseTransition: !state.pauseTransition,
      };
    case SETEAR_COLOR:
      return {
        ...state,
        modoColor: action.payload,
      };
    default:
      // Devuelve el estado sin cambios para cualquier otra accin
      return state;
  }
};

export default rootReducer;
